import { ComposicaoPopupModalComponent } from './popup/composicao-popup-modal.component';
import { NgModule, LOCALE_ID } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { HttpClientModule } from '@angular/common/http';
import { SelectModule } from 'ng2-select';
import { LaddaModule } from 'angular2-ladda';
import { ModalModule } from 'ng2-bootstrap/modal';

import { PopupModalModule } from 'app/shared/modal/popup-modal.module';
import { ProdutosService } from './produtos.service';
import { CategoriasService } from './../categorias/categoria.service';
import { SharedComponentModule } from './../../shared/shared-component.module';
import { ProdutosRoutingModule } from './produtos-routing.module';
import { ListarProdutosComponent } from './listar/listar-produtos.component';
import { CreateProdutosComponent } from './create/create-produtos.component';
import { EditProdutosComponent } from './edit/edit-produtos.component';
import { StorageService } from './../../../services/storage.service';
import { NumberOnlyDirective } from '../../shared/directive/number-only.directive';
import { TextMaskModule } from 'angular2-text-mask';
import { CurrencyMaskModule } from "ng2-currency-mask";
import { UnidadeMedidaService } from './../config/unidademedida/unidade-medida.service';

@NgModule({
  imports: [
    CommonModule,
    FormsModule,
    ModalModule.forRoot(),
    SharedComponentModule,
    ReactiveFormsModule,
    PopupModalModule,
    HttpClientModule,
    ProdutosRoutingModule,
    SelectModule,
    LaddaModule,
    TextMaskModule,
    CurrencyMaskModule
  ],
  declarations: [
    ListarProdutosComponent,
    CreateProdutosComponent,
    EditProdutosComponent,
    ComposicaoPopupModalComponent
  ],
  providers:[
    ProdutosService,
    CategoriasService,
    UnidadeMedidaService,
    StorageService,
    ComposicaoPopupModalComponent
  ]
})
export class ProdutosModule { }



