import { CategoriaDTO } from 'models/categoria.dto';
import { ProdutoComposicaoDTO } from './../../../models/produto-composicao.dto';
import { AbstractBaseComponent } from 'config/core/abstract-base.component';

import { ComboDTO } from 'models/combo.dto';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { Component, OnInit, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { Location } from '@angular/common';
import { EventEmitterService } from 'app/shared/event/event-emitter.services';
import { SelectComponent, SelectItem } from 'ng2-select';


export abstract class AbstractProdutoComponent extends AbstractBaseComponent {


  public checkUsoInterno: boolean = false;
  public checkProdutoComposto: boolean = false;

  public prodComp: ComboDTO;
  public categorias: CategoriaDTO[];

  public produtosComposicoes: ProdutoComposicaoDTO[] = [];
  public comboCategorias: ComboDTO[] = [];


  constructor(
    public fb: FormBuilder,
    public location: Location) {
    super();
  }


  public voltar() {
    this.location.back();
  }

  mudarImage(evento) {
    this.atualizarFoto(evento.foto);
  }

  private atualizarFoto(foto: any) {
    this.urlFoto = foto;
  }

  public alterarCheckUsoInterno(valor: boolean) {
    this.formulario.patchValue({
      usoInterno: valor ? 'S' : 'N'
    });
  }

  public alterarCheckProdutoComposto(valor: boolean) {
    this.checkProdutoComposto = valor;
    this.formulario.patchValue({
      produtoComposto: valor ? 'S' : 'N'
    });
  }

  protected criarFormulario() {
    this.formulario = this.fb.group({
      idProduto: [''],
      nmProduto: ['', [Validators.required, Validators.minLength(5), Validators.maxLength(25)]],
      categoria: ['', Validators.required],
      preco: ['', Validators.required],
      produtoComposto: ['N'],
      status: ['A'],
      usoInterno: ['N'],
      urlImagem: ['']
    })
  }

  confirmarCompProd(value: any) {
    let prod = {} as ProdutoComposicaoDTO;
    prod.idProduto = value.idProdutoPai;
    prod.idProdutoComposicao = value.produtoComp[0].id;
    prod.nmProdutoComposicao = value.produtoComp[0].text;
    prod.idUnidadeMedida = value.unidade[0].id;
    prod.nmUnidadeMedida = value.unidade[0].text;
    prod.qtdUtilizada = Number(value.qtdUtilizada)
    prod.mostrarUnidade = value.mostrarUnidade
    prod.emAlteracao = value.emAlteracao;

    let position = this.produtosComposicoes.findIndex(x => x.idProdutoComposicao == prod.idProdutoComposicao);
    if (position == -1) {
      this.produtosComposicoes.push(prod);
    } else {
      if (prod.emAlteracao) {
        this.produtosComposicoes[position] = prod;
      } else {
        this.produtosComposicoes[position].qtdUtilizada += prod.qtdUtilizada
      }
    }
  }

  excluirItem(item: ProdutoComposicaoDTO) {
    this.produtosComposicoes =
      this.produtosComposicoes
        .filter((prod: ProdutoComposicaoDTO) => prod.idProdutoComposicao != item.idProdutoComposicao)
  }
}



