import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Location } from '@angular/common';


import { ProdutoDTO } from 'models/produto.dto';
import { ProdutosService } from './../produtos.service';
import { CategoriasService } from './../../categorias/categoria.service';
import { AbstractProdutoComponent } from '../abstract-produto.component';
import { EventEmitterService } from 'app/shared/event/event-emitter.services';




@Component({
  selector: 'app-create-catetorias',
  templateUrl: './create-produtos.component.html',
  styleUrls: ['./create-produtos.component.scss']
})
export class CreateProdutosComponent extends AbstractProdutoComponent implements OnInit {



  constructor(
    public fb: FormBuilder,
    public location: Location,
    public categoriasService: CategoriasService,
    public produtosService: ProdutosService,
    public router: Router) {
    super(fb,location);
  }

  ngOnInit() {
    this.criarFormulario();
    this.categoriasService.findAll()
      .subscribe(
        response => { this.comboCategorias = this.preencharCombo(response, 'id', 'nome'); }
        //, error => console.log(error)
      );
  }

  confirmar(value: any) {
    //let categoria: CategoriaDTO = {id:'1', nome:'2das'};
    
    let prod = {} as ProdutoDTO;
    prod.id = value.idProduto;
    prod.nmProduto = value.nmProduto;
    prod.preco = value.preco;
    prod.produtoComposto = value.produtoComposto;
    prod.status = 'A';
    prod.usoInterno = value.usoInterno;
    prod.categoria = { id: value.categoria[0].id, nome: value.categoria[0].text }
    prod.urlImagem = this.urlFoto;
    prod.composicoes= this.produtosComposicoes;

    this.produtosService.salvar(prod)
      .subscribe(response => {
        EventEmitterService.emitirMensagemToasterSucesso('Categorias', 'Categoria incluída com sucesso!!!');
        this.router.navigate(['/produtos/listar']);
      },
        error => {
          //console.log('error de validação:', error.error);
        });

  }

}



