import { Component, Input, Output, ViewChild, OnInit, EventEmitter } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';


import { ProdutoComposicaoDTO } from 'models/produto-composicao.dto';
import { ModalDirective } from 'ng2-bootstrap/modal/modal.component';
import { ComboDTO } from './../../../../models/combo.dto';
import { CategoriasService } from './../../categorias/categoria.service';
import { ProdutosService } from './../produtos.service';
import { EventEmitterService } from './../../../shared/event/event-emiter.services';

import { AbstractBaseComponent } from 'config/core/abstract-base.component';
import { UnidadeMedidaService } from './../../config/unidademedida/unidade-medida.service';

@Component({
  selector: 'composicao-popup-modal',
  styleUrls: ['./composicao-popup-modal.component.scss'],
  templateUrl: './composicao-popup-modal.component.html'

})
export class ComposicaoPopupModalComponent extends AbstractBaseComponent implements OnInit {

  titulo: string = "Confirmar";
  checkMostrar: boolean = true;
  comboCompUnidades: ComboDTO[] = []
  comboCompProdutos: ComboDTO[] = []
  formProdComp: FormGroup;

  @ViewChild('modalConfirm') confirma;

  @Output() onClickConfirm = new EventEmitter<any>();

  constructor(
    public fb: FormBuilder,
    public unidadeService: UnidadeMedidaService,
    public produtosService: ProdutosService) {
    super();
  }

  ngOnInit() {

    this.criarFormProdComp();
    this.produtosService.findAllProdutoInterno()
      .subscribe(
        response => { this.comboCompProdutos = this.preencharCombo(response, 'id', 'nmProduto'); }
        //, error => 
      );

    this.unidadeService.findAllAtivo()
      .subscribe(
        response => { this.comboCompUnidades = this.preencharCombo(response, 'id', 'sigla'); }
        //, error => console.log(error)
      )
  }

  show() {
    this.criarFormProdComp();
    this.confirma.show();
  }

  showAlteracao(item: ProdutoComposicaoDTO) {
    this.alterarProdutoComp(item)
    this.confirma.show();
  }

  btnConfirmar(value: any) {
    this.onClickConfirm.emit(value);
    this.confirma.hide();
  }

  criarFormProdComp() {
    this.formProdComp = this.fb.group({
      idProdutoPai: [],
      produtoComp: ['', Validators.required],
      unidade: ['', Validators.required],
      qtdUtilizada: ['', Validators.required],
      mostrarUnidade: ['S'],
      emAlteracao:[false]
    })
  }

  compProdCheckMostrar(valor: boolean) {
    this.formProdComp.patchValue({
      mostrarUnidade: valor ? 'S' : 'N'
    });
  }


  private alterarProdutoComp(item: ProdutoComposicaoDTO) {
    this.formProdComp.patchValue({
      produtoComp: [{id: item.idProdutoComposicao, text: item.nmProdutoComposicao }],
      unidade: [{ id: item.idUnidadeMedida, text: item.nmUnidadeMedida }],
      qtdUtilizada: item.qtdUtilizada,
      mostrarUnidade: item.mostrarUnidade,
      emAlteracao:true
    });
  }

}



