import { ComboDTO } from 'models/combo.dto';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { Location } from '@angular/common';

import { ProdutoDTO } from 'models/produto.dto';
import { ProdutosService } from './../produtos.service';
import { CategoriasService } from './../../categorias/categoria.service';
import { AbstractProdutoComponent } from '../abstract-produto.component';
import { EventEmitterService } from 'app/shared/event/event-emitter.services';


@Component({
  templateUrl: './edit-produtos.component.html',
  styleUrls: ['./edit-produtos.component.scss']
})
export class EditProdutosComponent extends AbstractProdutoComponent implements OnInit {

  constructor(
    public fb: FormBuilder,
    public route: ActivatedRoute,
    public location: Location,
    public categoriasService: CategoriasService,
    public produtosService: ProdutosService,
    public router: Router) {
    super(fb, location);
  }

  ngOnInit() {
    this.criarFormulario();

    this.route.params.forEach((params: Params) => {
      let id = params['id'];
      this.produtosService.findById(id).subscribe( resp =>{
          let produto = resp as ProdutoDTO;

          this.formulario = this.fb.group({
            idProduto: [produto.id],
            nmProduto: [produto.nmProduto, [Validators.required, Validators.minLength(5), Validators.maxLength(25)]],
            categoria: [[{id: produto.categoria.id, text: produto.categoria.nome}], Validators.required],
            preco: [produto.preco, Validators.required],
            produtoComposto: [produto.produtoComposto],
            status: [produto.status],
            usoInterno: [produto.usoInterno]
            //urlImagem: [produto.urlImagem]
          })
          this.checkUsoInterno = produto.usoInterno =='S';
          if (produto.produtoComposto =='S'){
            this.checkProdutoComposto= true;
            this.produtosComposicoes = produto.composicoes;
          }
      })
    })
  
    this.categoriasService.findAll()
      .subscribe(
        response => { this.comboCategorias = this.preencharCombo(response, 'id', 'nome'); }
        //, error => console.log(error)
      );


  }

  confirmar(value: any) {
    //let categoria: CategoriaDTO = {id:'1', nome:'2das'};

    let prod = {} as ProdutoDTO;
    prod.id = value.idProduto;
    prod.nmProduto = value.nmProduto;
    prod.preco = value.preco;
    prod.produtoComposto = value.produtoComposto;
    prod.status = value.status;
    prod.usoInterno = value.usoInterno;
    prod.categoria = { id: value.categoria[0].id, nome: value.categoria[0].text }
    prod.urlImagem = this.urlFoto;
    prod.composicoes = this.produtosComposicoes;
    
    this.produtosService.atualizar(prod)
      .subscribe(response => {
        EventEmitterService.emitirMensagemToasterSucesso('Categorias', 'Categoria incluída com sucesso!!!');
        this.router.navigate(['/produtos/listar']);
      },
        error => {
          //console.log('error de validação:', error.error);
        });
  }

}


